	module.exports = {"title":"Writing and The Power of Stories","tags":["Writing"],"date":"2007-02-24T22:47:21.000Z","layout":"post","body":"<p>I’ve been thinking lately about stories. The stories we tell others, the stories we tell ourselves, and the stories we find in books and on TV. There is a great power in stories. Stories change how we think and view the world. Marketers know this – every advertisement is a story. A particularly famous example of this is <a href=\"http://en.wikipedia.org/wiki/Jared_Fogle\" title=\"Jared the Subway Guy\">Jared the Subway guy</a>. His story of how he lost weight from eating subways powerfully sells the idea that Subway’s food is healthy.</p>\n<p>Earlier I was looking at the website for the <a href=\"http://www.python.org/\" title=\"Python\">Python programming language</a>. One might think a website devoted to a programming language, a technical and geeky subject, wouldn’t have any stories. But no, programming languages have stories too apparently, there are some 33 “<a href=\"http://www.python.org/about/success\">success stories</a>” written by users of Python.</p>\n<p>Oftentimes, while reading or listening, we only remember the stories the speech or book. Jesus Christ was a master at teaching with stories, or parables (a story with a moral) as they are called in the Bible. In his seminal book, “<a href=\"http://en.wikipedia.org/wiki/Jesus_the_Christ_(book_by_James_E._Talmage)\" title=\"Jesus the Christ -- from Wikipedia\">Jesus the Christ</a>”, <a href=\"http://en.wikipedia.org/wiki/James_E._Talmage\" title=\"James Talmage\">James Talmage</a> wrote:</p>\n<blockquote>\n<p>[A] simple story will live, even in minds which for the time being are incapable of comprehending any meaning beyond that of the commonplace story itself. Many a peasant who heard the little incident of the sower and the four kinds of soil, of the tares sown by an enemy at night, of the seed that grew through the planter had temporarily forgotten it, would be reminded by the recurring circumstances of his daily work. . . and then, when time and experience, including suffering perhaps, had prepared them for deeper thought, they would find the living kernel of gospel truth within the husk of the simple tale.</p>\n</blockquote>\n<p>As social animals, we love to tell stories. We turn every event in our life into a story. A friend meeting another friend, the first word of of their mouths is, “I’ve got to tell you what happened last night”. We have this strong impulse inside us to turn our lives into a story.</p>\n<p>This blog post started to form when I was thinking about journal writing. I was trying to understand what compels me to write in my journal and why is the experience of writing so powerful. While thinking, I had the insight that it’s because of stories.</p>\n<p>When I write in my journal I find my story and myself. I weave the threads of my life, past, present, and future, into a coherent story. I create a plot line, a protagonist (me hopefully), antagonists, and an destiny. I decide why what happened, happened, I decide what needs to happen and if my story is in need of a plot or character shift.</p>\n<p>“Know yourself”, the Philosopher shouts. I write to know myself. I write as a historian, to know my past. I write as a journalist, to know my present. And I must write as a prophet, write my most powerful stories, to know my future.</p>\n<p>What if my life doesn’t have a story? Then my life is meaningless, without root or branch. I am not stable but pushed about by every fad or fashion. So in short, writing a journal helps me discover my story. And knowing my story gives me purpose and direction.</p>\n"}

