	module.exports = {"title":"Get a Wiki: Why Your Organization needs a Wiki","tags":["enterprise2.0"],"date":"2007-05-05T20:03:57.000Z","layout":"post","body":"<h3>Everyone can participate</h3>\n<p>Wikis flatten rigid hierachies and ease the flow of information. Bright ideas can come from anywhere within your organization. Wikis let everyone participate and help the best ideas to emerge. Wikis make it easy for large number of people to collaborate.</p>\n<h3>A Wikipedia for your organization</h3>\n<p>Just like Wikipedia documents the world’s information, your new wiki will become the repository for your organization’s knowledge. How easy is it to find information on your intranet? Change that with a wiki. Everything from legal documents, to meeting notes, to project collaboration, to pictures from your last party can and should be stored on your wiki. A wiki is a great place to store all the “stuff” your organization generates in a simple, easy, findable manner. Free information from the grips of the inbox.</p>\n<h3>Low cost</h3>\n<p>While there are plenty of commercial wiki companies that’d love to sell you a wiki – and provide great value, there’s no excuse for not jumping into the wiki world with the large number of great free open-source wikis available. Enthusiastic wiki-lovers the world over have banded together to create enterprise-ready wiki software. My favorite is <a href=\"http://www.twiki.org\">TWiki</a> but don’t stop looking there. The <a href=\"http://www.wikimatrix.org/\">WikiMatrix</a> is an easy way to learn about the many wikis available.</p>\n<p>Thousands of organizations are using wikis. Read customer success stories from organizations using <a href=\"http://twiki.org/cgi-bin/view/Main/TWikiSuccessStories\">TWiki</a>, <a href=\"http://www.socialtext.com/customerstories\">Socialtext</a>, and <a href=\"http://www.atlassian.com/software/confluence/casestudies/default.jsp\">Confluence</a>.</p>\n"}

