	module.exports = {"title":"Power Law Graphs from classroom community website","tags":["drupal","Education","elearning2.0","Learning","Power Law","social software"],"date":"2008-02-15T18:18:04.000Z","layout":"post","body":"<p>Researchers have shown that the distribution of many natural and social phenomenons follow what’s called the <a href=\"http://en.wikipedia.org/wiki/Power_law\">power law</a>. Power laws are known by other names such as the 20-80 rule (80% of wealth is controlled by 20% of the population), the long tail, Winner-Take-All, etc.</p>\n<p>Here is an example power law graph from Wikipedia:</p>\n<p><img src=\"./300px-Long_tail.svg.png\" alt=\"Power Law\"></p>\n<p><a href=\"http://www.shirky.com/writings/powerlaw_weblog.html\">Many</a> <a href=\"http://www.kottke.org/03/02/weblogs-and-power-laws\">have</a> <a href=\"http://ross.typepad.com/blog/2006/04/power_law_of_pa.html\">written</a> that almost all measures of web site usage follow the same pattern whether that be page views, in-bound links, etc.</p>\n<p>This past semester I built an <a href=\"http://isyscore.byu.edu/drupal\">e-learning site</a> for two sections of an upper-level university course at Brigham Young University. Now that the semester is finished, we’ve started to analyze the data we’ve collected on how students used the site.</p>\n<p>And what did I notice when I started to graph the data? Why the power law of course.</p>\n<p>So onto the graphs I made.</p>\n<p>First is a graph of the number of comments each student made on the class website throughout the semester. This is a good measure of their engagement with the online community. You can see that some students left as many as 40-60 comments throughout the semester and others left as little as 1 or two:<img src=\"./num_comments_per_student.png\" alt=\"Number of comments made by each student\"></p>\n<p>Another graph which follows the power law. This time a graph of the number of visits to the class website per student. Again we see a wide disparity between students. One student visited the site over 400 times! Which in a four month semester means they visited the site on average 3 times a day! And on the other end, a number of students only visited the site 5-10 times the entire semester.</p>\n<p><img src=\"./num_visits_to_site_per_student.png\" alt=\"Number of visits to the site made by each student\"></p>\n<h3>Why the Power Law?</h3>\n<p>So why does this happen? Why do some students participate on the class website so much more then other students? I’m not sure I know the answer to that. Why do some people write blogs, participate in Wikipedia, upload their photos to flickr, etc. and many others don’t?</p>\n<p>Do some students just not see <a href=\"/why-i-blog-part-1/\">the value of blogging</a>? Are some students more comfortable interacting online for whatever reason? Or on another tact, does it matter if some students participate more then others? After all, in any community, there’s always a core group which cares much more then others about the success of the community (read about the <a href=\"http://ross.typepad.com/blog/2006/04/power_law_of_pa.html\">power law of participation</a>). What type of participation in an online learning community meets the learning goals for a classroom?</p>\n<p>There’s a host of other ideas rumbling around in my head about power laws, the three types of networks (political, social, creative) and how all this relates to creating effective online learning/collaborative environments. But that will all have to wait for another day. In the meantime, here’s some links.</p>\n<h4>Extra Credit Reading:</h4>\n<p>Read these two articles by Ross Mayfield, <a href=\"http://radio.weblogs.com/0114726/2003/02/12.html#a284\">first this one</a>, <a href=\"http://radio.weblogs.com/0114726/2003/02/10.html#a281\">then this one</a>.</p>\n<p>Then two by Clay Shirky, <a href=\"http://www.shirky.com/writings/powerlaw_weblog.html\">&quot;Power Laws, Weblogs, and Inequality</a>&quot; and “<a href=\"http://shirky.com/writings/group_politics.html\">Social Software and the Politics of Groups</a>.”</p>\n<p>And one more on <a href=\"http://many.corante.com/archives/2003/12/22/think_group.php\">building vibrant communities</a>.</p>\n"}

