	module.exports = {"title":"Six principles for making new things","tags":["blogging","innovation"],"date":"2008-02-19T01:51:40.000Z","layout":"post","body":"<p>I love it when someone writes what I’m thinking about writing. Saves me time.</p>\n<p>Paul Graham posted a new essay today entitled &quot;<a href=\"http://www.paulgraham.com/newthings.html\">Six Principles for Making New Things</a>.&quot;<br>\nHere’s the juicy bit:</p>\n<blockquote>\n<p>I like to find (a) simple solutions (b) to overlooked problems © that actually need to be solved, and (d) deliver them as informally as possible, (e) starting with a very crude version 1, then (f) iterating rapidly.</p>\n</blockquote>\n<p><a href=\"http://www.paulgraham.com/newthings.html\">Read the rest.</a></p>\n<p>To add a few thoughts.</p>\n<p>When I think of overlooked problems I think of a bell curve. Most people/companies/countries are average: thinking average thoughts and doing things in an average way. Their average thoughts/actions lead to average results. If you want exceptional results, you have to act and think in ways that are exceptional. Average=dead, the <a href=\"http://www.edgeperspectives.com/\">edge is where the action</a> is at.</p>\n"}

