	module.exports = {"title":"How to become an expert","date":"2008-05-17T15:12:59.000Z","draft":true,"layout":"post","body":"<p>I’m a big fan of Steven Covey’s “Seven Habits of Highly Effective People” and of th freakonomics blog so when I made a connection today between the two, I knew I had to blog about it.</p>\n<p>A fundemental principle in Covey’s book is that private victory must proceed public victory.<br>\n1. Focus on technique as opposed to outcome.<br>\n2. Set specific goals.<br>\n3. Get good, prompt feedback, and use it.</p>\n<p>Don’t become an expert by trying to become an expert. You become an expert by molding yourself until you acquire the attributes of an expert. Then, naturally - almost without anymore effort on your part, people will recognize you as an expert.</p>\n<p>It’s a very common pattern for a young start-up to come out with an amazing new product and for the business press to wander how the start-up became so good so quickly. This ignores the years of trial and effort the start-up went through to get where it is.</p>\n<p>What do I take from this? I want to do great things in my life. How will I do this? Not by setting out tomorrow on some great task (don’t focus on the outcome) but by practicing basic techniques. Understanding myself, writing, reading, social skills, etc.<br>\nBy mastering the skils of greatness, then naturally I’ll be able to do great things.</p>\n"}

