	module.exports = {"title":"Announcing the first alpha release of the Drupal Memetracker module","tags":["drupal","memetracker","drupal planet","code release"],"date":"2008-07-01T21:26:22.000Z","layout":"post","body":"<p>I recently made my first alpha release of the Drupal Memetracker module. You can download it from the <a href=\"http://drupal.org/project/memetracker\">project page</a> on the Drupal community site.</p>\n<p>An screenshot from the latest code:</p>\n<p><a href=\"http://www.flickr.com/photos/82268668@N00/2616168771/\" title=\"Screenshot 6/27/08 by kylenumber5, on Flickr\"><img src=\"./2616168771_136c8c3e4a_o.png\" alt=\"Screenshot 6/27/08\"></a></p>\n<p>A quick refresher on what my project is all about taken from my <a href=\"/drupal-memetracker-module-my-google-summer-of-code-application/\">original proposal</a> to Google Summer of Code.</p>\n<blockquote>\n<h3>memetracker = smart aggregator:</h3>\n<p>An aggregator is software like Drupal Planet or PlanetPlanet that aggregates related content from many sources to display in a central location. But current aggregators are dumb. The dumb aggregator knows no better than to pull in new content and order it chronologically.</p>\n<p>A memetracker is a smart aggregator. It also knows how to pull in new content as a dumb aggregator but it’s much smarter about how it displays the content. It can analyze the text of the content and know which authors are talking about the same topic and then group them together. But not only will the smart aggregator group similar topics but it will also learn what topics and authors are most interesting to members of the community and display those first. No more will you have to scroll through a long list of content skipping over content you’re not interested in but instead, new content will be nicely organized by meme and interestingness in a neat compact form.</p>\n</blockquote>\n<p>To join in the work of building an open source memetracker, join the <a href=\"http://groups.drupal.org/memetracker\">Memetracker group</a> at <a href=\"http://groups.drupal.org\">groups.drupal.org</a>.</p>\n"}

