	module.exports = {"title":"Process is an embedded reaction to prior stupidity","tags":["process"],"date":"2008-11-30T02:39:36.000Z","layout":"post","body":"<p>Since I read this quote by Clay Shirky, “<a href=\"http://many.corante.com/archives/2003/09/17/process_is_an_embedded_reaction_to_prior_stupidity.php\">process is an embedded reaction to prior stupidity</a>” it’s been at the top of my very long Shirky quote list. For someone like me who thrives on doing things, bureaucracy is a nightmare.</p>\n<p>Another of my favorite authors, Paul Graham, wrote a great essay along the same vein today, talking about the <a href=\"http://www.paulgraham.com/artistsship.html\">gradual build-up of checks and processes</a> and the very real cost those checks impose upon all of us.</p>\n<p>Some of my favorite parts:</p>\n<blockquote>\n<p>One of the differences between big companies and startups is that big companies tend to have developed procedures to protect themselves against mistakes. A startup walks like a toddler, bashing into things and falling over all the time. A big company is more deliberate.</p>\n<p>The gradual accumulation of checks in an organization is a kind of learning, based on disasters that have happened to it or others like it. After giving a contract to a supplier who goes bankrupt and fails to deliver, for example, a company might require all suppliers to prove they’re solvent before submitting bids.</p>\n<p>As companies grow they invariably get more such checks, either in response to disasters they’ve suffered, or (probably more often) by hiring people from bigger companies who bring with them customs for protecting against new types of disasters.</p>\n<p>It’s natural for organizations to learn from mistakes. The problem is, people who propose new checks almost never consider that the check itself has a cost.</p>\n<p>…</p>\n<p>Whenever someone in an organization proposes to add a new check, they should have to explain not just the benefit but the cost. No matter how bad a job they did of analyzing it, this meta-check would at least remind everyone there had to be a cost, and send them looking for it.</p>\n<p>If companies started doing that, they’d find some surprises. Joel Spolsky recently spoke at Y Combinator about selling software to corporate customers. He said that in most companies software costing up to about $1000 could be bought by individual managers without any additional approvals. Above that threshold, software purchases generally had to be approved by a committee. But babysitting this process was so expensive for software vendors that it didn’t make sense to charge less than $50,000. Which means if you’re making something you might otherwise have charged $5000 for, you have to sell it for $50,000 instead.</p>\n<p>The purpose of the committee is presumably to ensure that the company doesn’t waste money. And yet the result is that the company pays 10 times as much.</p>\n<p>…</p>\n<p>In more recent times, Sarbanes-Oxley has practically destroyed the US IPO market [Ha! My accounting teachers never mentioned this]. That wasn’t the intention of the legislators who wrote it. They just wanted to add a few more checks on public companies. But they forgot to consider the cost. They forgot that companies about to go public are usually rather stretched, and that the weight of a few extra checks that might be easy for General Electric to bear are enough to prevent younger companies from being public at all.</p>\n<p>…</p>\n<p>Programmers are unlike many types of workers in that the best ones actually prefer to work hard. This doesn’t seem to be the case in most types of work. When I worked in fast food, we didn’t prefer the busy times. And when I used to mow lawns, I definitely didn’t prefer it when the grass was long after a week of rain.</p>\n<p>Programmers, though, like it better when they write more code. Or more precisely, when they release more code. Programmers like to make a difference. Good ones, anyway.</p>\n</blockquote>\n<p>Great stuff, <a href=\"http://www.paulgraham.com/artistsship.html\">read the whole thing</a>.</p>\n"}

