	module.exports = {"title":"Presentation from OpenEd 2009","tags":["social software","opened09"],"date":"2009-08-13T21:38:59.000Z","layout":"post","body":"<p>I presented at the OpenEd Conference this year in Vancouver BC. Great fun.</p>\n<h3>Video</h3>\n<p>My section starts ~20 minutes in.</p>\n<p>\n  <embed flashvars=\"loc=%2F&amp;autoplay=false&amp;vid=1978484\" width=\"480\" height=\"386\" allowfullscreen=\"true\" allowscriptaccess=\"always\" src=\"http://www.ustream.tv/flash/video/1978484\" type=\"application/x-shockwave-flash\" />\n</p>\n<h3>Slides</h3>\n<p>\n  <iframe src=\"http://www.slideshare.net/slideshow/embed_code/1857718\" width=\"427\" height=\"356\" frameborder=\"0\" marginwidth=\"0\" marginheight=\"0\" scrolling=\"no\" style=\"border:1px solid #CCC; border-width:1px 1px 0; margin-bottom:5px; max-width: 100%;\" allowfullscreen> </iframe>\n</p>\n<h3>Resources listed in the presentation:</h3>\n<ul>\n<li>“Here Comes Everyone” by Clay Shirky <a href=\"http://www.amazon.com/Here-Comes-Everybody-Organizing-Organizations/dp/1594201536\">http://www.amazon.com/Here-Comes-Everybody-Organizing-Organizations/dp/1594201536</a></li>\n<li>“Linked: How Everything Is Connected to Everything Else and What It Means” by Albert-Laszlo Barabasi Excellent intro to science of networks</li>\n<li>Introduction to using patterns in web design – <a href=\"http://37signals.com/papers/introtopatterns/\">http://37signals.com/papers/introtopatterns/</a></li>\n<li>“Designing for Social Traction” – <a href=\"http://bokardo.com/archives/designing-for-social-traction-slide-deck/\">http://bokardo.com/archives/designing-for-social-traction-slide-deck/</a></li>\n<li>“Drupal for Education and E-Learning” by Bill Fitzgerald</li>\n<li>“The Art of the Community” – upcoming book by Ubuntu community manager – <a href=\"http://www.artofcommunityonline.org/\">http://www.artofcommunityonline.org/</a></li>\n<li>“The Fifth Discipline: The Art &amp; Practice of The Learning Organization” by Peter Senge</li>\n<li>“The Only Sustainable Edge: Why Business Strategy Depends on Productive Friction and Dynamic Specialization” by John Hagel and John Seely Brown</li>\n<li>“Minds on Fire: Open Education, the Long Tail, and Learning 2.0” by John Seely Brown and Richard P. Adler <a href=\"http://www.educause.edu/EDUCAUSE+Review/EDUCAUSEReviewMagazineVolume43/MindsonFireOpenEducationtheLon/162420\">http://www.educause.edu/EDUCAUSE+Review/EDUCAUSEReviewMagazineVolume43/MindsonFireOpenEducationtheLon/162420</a></li>\n<li>“Design Innovation and Innovation Ecotones” by Ann Pendleton-Jullian <a href=\"http://president.asu.edu/node/705\">http://president.asu.edu/node/705</a></li>\n<li>“Building Web 2.0 Reputation Systems” – <a href=\"http://buildingreputation.com/\">http://buildingreputation.com/</a></li>\n<li>“Enterprise 2.0: New Collaborative Tools for Your Organization’s Toughest Challenges” by Andrew McAfee</li>\n</ul>\n<h3>Social software Patterns</h3>\n<ul>\n<li>“Designing Social Interfaces” – <a href=\"http://www.designingsocialinterfaces.com/patterns.wiki/index.php?title=Main_Page\">http://www.designingsocialinterfaces.com/patterns.wiki/index.php?title=Main_Page</a></li>\n<li>“Community Design Patterns” – <a href=\"http://www.slideshare.net/pecus/online-communities-design-patterns-255635\">http://www.slideshare.net/pecus/online-communities-design-patterns-255635</a></li>\n<li>“Social Software Pattern Language” – <a href=\"http://wikis.sun.com/display/ASSPL/A+Social+Software+Pattern+Language\">http://wikis.sun.com/display/ASSPL/A+Social+Software+Pattern+Language</a></li>\n</ul>\n"}

