	module.exports = {"title":"McLuhan predicts newspaper's demise in 1965.","tags":["newspapers"],"date":"2009-12-19T20:29:17.000Z","layout":"post","body":"<p>Reading through “<a href=\"http://www.amazon.com/Understanding-Media-Extensions-Marshall-McLuhan/dp/0262631598\">Understanding Media: The Extensions of Man</a>” by Marshall McLuhan, I came across this gem of a quote:</p>\n<blockquote>\n<p>The classified ads (and stock-market quotations) are the bedrock of the press. Should an alternative source of easy access to such diverse daily information be found, the press will fold.</p>\n</blockquote>\n<p>And I think we all know how that’s turned out.</p>\n"}

