	module.exports = {"title":"An Education Syllogism","tags":["Education","Learning","social media","social networking","social learning","syllogism"],"date":"2010-02-02T22:24:43.000Z","layout":"post","body":"<p>Schools exist to prepare students to thrive in the environment where they live.</p>\n<p>The environment we live in has changed significantly in the past fifty years from a mechanistic to an electronic world.</p>\n<p>Therefore, schools must change their methods for how they prepare students from mechanistic preparation (<a href=\"http://www.jonmott.com/blog/2008/07/learner-presence-in-course-management-systems/\">fixed length/content courses</a>, <a href=\"http://www.everythingismiscellaneous.com/\">categorized knowledge</a>) to electronic preparation (<a href=\"/marshall-mcluhan-education\">pattern recognition</a>, <a href=\"/organizing-university-learning-moving-beyond-classroom\">rhizomic</a> <a href=\"http://davecormier.com/edblog/2008/06/03/rhizomatic-education-community-as-curriculum/\">learning</a>).</p>\n"}

