	module.exports = {"title":"My social learning Drupal distribution Eduglu is out for testing","tags":["drupal","Education","Learning","social learning","Eduglu"],"date":"2010-03-22T20:19:25.000Z","layout":"post","body":"<p>The first alpha of Eduglu is out and ready for testing. Download it at <a href=\"http://community.eduglu.org/release-announcement/node/8\">http://community.eduglu.org/release-announcement/node/8</a></p>\n<h2>What I hope to accomplish with this first Alpha?</h2>\n<p>My hope is that releasing this first of Eduglu will start a conversation about what shape should a Drupal educational distro take. Eduglu is a partial-implementation of my vision of what an Drupal education distro should be. But I’m well aware that my vision is limited in many ways and that the problems I’ve designed Eduglu to solve probably don’t even begin to cover all the different problems you as a collective are trying to solve.</p>\n<p>So let’s get the conversation going. What do you think Eduglu should be? What problems are you facing? What would your dream social learning website be? If you’re building some sort of social learning tool on Drupal already (as I know many of you are), what are you trying to do and how?</p>\n<p>So this first alpha is a chance for us to talk about what Eduglu should be. Once we’ve decided what Eduglu should be, we can start pushing toward a stable Beta release, fixing bugs and adding missing features.</p>\n<p><a href=\"http://community.eduglu.org\">Let’s get started!</a></p>\n<h2>Screencast of installation and initial setup of Eduglu</h2>\n<p>\n  <object width=\"660\" height=\"525\"><param name=\"movie\" value=\"http://www.youtube.com/v/-5SLfDKxTi8&hl=en_US&fs=1&rel=0&hd=1&border=1\"></param><param name=\"allowFullScreen\" value=\"true\"></param><param name=\"allowscriptaccess\" value=\"always\"></param><embed src=\"http://www.youtube.com/v/-5SLfDKxTi8&hl=en_US&fs=1&rel=0&hd=1&border=1\" type=\"application/x-shockwave-flash\" allowscriptaccess=\"always\" allowfullscreen=\"true\" width=\"660\" height=\"525\"></embed></object>\n</p>\n"}

