	module.exports = {"title":"Conversation","tags":["Education","social learning","Eduglu"],"date":"2010-04-26T20:48:42.000Z","layout":"post","body":"<p>From British political philosopher Michael Oakeshott:</p>\n<blockquote>\n<p>Conversation is not . . . a contest where a winner gets a prize, . . . it is an endless, unrehearsed, intellectual adventure in which in imagination we enter a variety of modes of understanding the world and ourselves. And, we are not disconcerted by the differences, or dismayed by the inconclusiveness of it all.</p>\n</blockquote>\n<p>I really like that. Good conversation is good for the soul.</p>\n<p>Taken from <a href=\"http://andrewmcafee.org/\">Andrew McAfee’s</a> “<a href=\"http://www.amazon.com/Enterprise-2-0-Collaborative-Organizations-Challenges/dp/1422125874/ref=sr_1_2?ie=UTF8&amp;s=books&amp;qid=1272314809&amp;sr=8-2\">Enterprise 2.0</a>” book.</p>\n"}

