	module.exports = {"title":"Richard Hamming on Luck","tags":["luck"],"date":"2011-04-09T18:26:54.000Z","layout":"post","body":"<p>From Richard Hamming’s classic and must-read talk, “<a href=\"http://www.cs.virginia.edu/~robins/YouAndYourResearch.html\">You and Your Research</a>”.</p>\n<blockquote>\n<p>There is indeed an element of luck, and no, there isn’t. The prepared mind sooner or later finds something important and does it. So yes, it is luck. <em>The particular thing you do is luck, but that you do something is not.</em></p>\n</blockquote>\n"}

